'use client';

import { useEffect, useState } from 'react';
import { motion } from 'framer-motion';
import Link from 'next/link';
import EventCard from './EventCard';
import { EventCategory } from '@/constants/events';

const UpcomingEvents = () => {

    const [events, setEvents] = useState<EventCategory[]>([]);
    const [loading, setLoading] = useState<boolean>(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchEvents = async () => {
            setLoading(true);
            setError(null);

            try {
                const res = await fetch(`${process.env.API_URL}/api/events?upcoming}`);
                if (!res.ok) throw new Error('Failed to fetch events');

                const data = await res.json();
                setEvents(data.events); // assuming backend responds like: { events: [...] }
            } catch (err: any) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };
        fetchEvents();
    },[]);

    return (
        <section className="py-12 bg-gray-50">
            <div className="container mx-auto px-4">
                {/* العنوان وأزرار الفلترة */}
                <div className="flex flex-col md:flex-row justify-between items-center mb-8">
                    <h2 className="text-2xl font-bold text-gray-800 mb-4 md:mb-0">Upcoming Events</h2>
                </div>

                {/* حالات التحميل أو الخطأ */}
                {loading && <p className="text-center text-gray-500">Loading events...</p>}
                {error && <p className="text-center text-red-500">{error}</p>}

                {/* شبكة البطاقات */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
                    {!loading &&
                        !error &&
                        events.map((event) => <EventCard key={event.id} className="" event={event} />)}
                </div>

                {/* زر عرض الكل */}
                <div className="text-center">
                    <motion.button
                        whileHover={{ scale: 1.05 }}
                        whileTap={{ scale: 0.95 }}
                        className="bg-purple-600 text-white px-6 py-3 rounded-full text-sm font-medium"
                    >
                        <Link href="/events">See All Events</Link>
                    </motion.button>
                </div>
            </div>
        </section >
    );
};

export default UpcomingEvents;