"use client"
import { motion } from 'framer-motion';
import { EventCategory } from '@/constants/events';
import { Group, MapPin, University, User, UserCog2 } from 'lucide-react';
import Image from 'next/image';
const EventCard = ({ event, className }: { event: EventCategory, className: String }) => {
    return (
        <>
            <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                className={`bg-white rounded-lg shadow-md overflow-hidden border border-gray-100 hover:shadow-lg transition-shadow ${className}`}
            >
                <div className='w-full'>
                    <Image
                        src={event.imageURL}
                        alt={event.title}
                        className='w-full h-48 object-cover'
                        width={200}
                        height={300}
                    />
                </div>
                {/* رأس البطاقة */}
                < div className="border-b border-gray-100" >
                    <div className="flex items-center justify-center" >
                        {
                            event.isSpecial && (
                                <span className="bg-yellow-100 text-yellow-800 text-xs px-2 py-1 rounded-full" >
                                    REGISTRATIONS ARE NOW OPEN
                                </span>
                            )
                        }
                    </div>
                </div>

                {/* محتوى البطاقة */}
                <div className="p-4" >
                    <div>
                        <h3 className="text-lg font-bold text-gray-800 mb-1" >Host : { } </h3>
                    </div>
                    <div className='flex justify-between items-center'>
                        <div>
                            <h3 className="text-lg font-bold text-gray-800 mb-1" >Title : {event.title} </h3>
                            < p className="text-sm text-gray-500 mb-3" > By {event.department} </p>
                        </div>
                        <div className="flex flex-col items-center" >
                            <span className="text-2xl font-bold text-gray-800" > {event.time} </span>
                            < span className="text-sm font-medium text-purple-500 uppercase" > {event.date} </span>
                        </div>
                    </div>
                    < div className="space-y-2 mb-4" >
                        < span className="text-xs text-gray-700" >{event.description}</span>
                    </div>
                    < div className="flex space-y-2" >
                        <User className='text-xs text-gray-500 pe-2' />
                        <span>registered :{ }</span>
                    </div>
                    < div className="flex space-y-2" >
                        <University className='text-xs text-gray-500 pe-2' />
                        <span>capacity : {event.capacity }</span>
                    </div>

                    {
                        event.prices && (
                            <div className="flex gap-2 mb-4" >
                                {
                                    event.prices.map((price, i) => (
                                        <span key={i} className="bg-gray-100 text-gray-800 px-2 py-1 rounded text-sm" >
                                            {price.toFixed(2)}
                                        </span>
                                    ))
                                }
                            </div>
                        )
                    }
                    <div className='flex items-center justify-between'>
                        <p className="text-xs text-gray-500" > {event.location} </p>
                        <MapPin className='text-xs text-gray-500' />
                    </div>
                </div>
            </motion.div>
        </>
    );
}
export default EventCard;